import { applyMiddleware, combineReducers, createStore } from "redux";
import profileReducer from "./reducer/reducer-profile";
import dialogsReducer from "./reducer/reducer-dialogs";
import sidebarReducer from "./reducer/reducer-sidebar";
import usersReducer from "./reducer/reducer-users";
import authReducer from "./reducer/reducer-auth";
import thunkMiddleware from "redux-thunk";
import { reducer as formReducer } from "redux-form";
import appReducer from "./reducer/reducer-app";

let reducers = combineReducers({
  profilePage: profileReducer,
  dialogsPage: dialogsReducer,
  sidebar: sidebarReducer,
  usersPage: usersReducer,
  auth: authReducer,
  form: formReducer,
  app: appReducer,
});

let store = createStore(reducers, applyMiddleware(thunkMiddleware));

export default store;
